import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { ReduceIndent as Button } from '@ali/we-toolbar';
import { PluginRoles } from '@ali/4ever-bamboo';
import { DECREASE_HOT_KEY_TIP } from '../utils/hotKeyTips';
import { decreaseIndent } from '../actions';
;

export interface DecreaseIndentButtonProps {
  controller: Controller;
  locale: Record<string, string>;
}

export default function DecreaseIndentButton(props: DecreaseIndentButtonProps) {
  const { controller, locale, ...rest } = props;
  const { isCollapsedAtEdge } = controller.value.selection;
  const isDisabled = isCollapsedAtEdge || controller.query('isSelectionInHr') || controller.query('isSelectionInListSymbol');

  const handleClick = React.useCallback(() => controller.run('onAction', decreaseIndent()), [controller]);

  return (
    <Button
      {...rest}
      shortcutText={DECREASE_HOT_KEY_TIP}
      testid="bi-toolbar-decindent"
      onClick={handleClick}
      disabled={!!isDisabled}
      role={PluginRoles.indentDecrease}
    />
  );
}
